.App {
    .container {
        margin: 5% auto 0 auto;
        max-width: 500px;
        color: #2F2F2F;
        padding: 0 20px;
        .header {
            font-weight: normal;
            margin: 0;
        }
        .preference-input {
            width: 100%;
            height: 40px;
            border-radius: 3px;
            border-style: solid;
            border-width: 1px;
            border-color: #c1c1c1;
            font-size: 14px;
            padding: 2px 10px;
            margin-top: 40px;
            box-sizing: border-box;
        }
        .error{
            color: #B33A3A;
            font-size: 10px;
        }
    }
}