.preferences-tags {
    margin-top: 20px;
    .tag {
        display: inline-block;
        font-size: 12px;
        background: #EFEDEE;
        margin: 0 10px 10px 0;
        padding: 5px 9px;
        border-radius: 20px;
        cursor: pointer;
        color: #6E6C6D;
        position: relative;
        max-width: 90%;
        &.remove-all {
            color: #B33A3A;
        }
        .text {
            vertical-align: middle;
            display: inline-block;
            max-width: 250px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            max-width: 85%;
        }
        .remove {
            color: #B33A3A;
            vertical-align: middle;
            display: inline-block;
            margin-left: 10px;
        }
    }
}